<template>
  <main class="content container" v-if="order">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ {{ $store.state.orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет
            письмо с&nbsp;деталями заказа. Наши менеджеры свяжутся с&nbsp;Вами
            в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key"> Получатель </span>
              <span class="dictionary__value">
                {{ order.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Адрес </span>
              <span class="dictionary__value">
                {{ order.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Телефон </span>
              <span class="dictionary__value">
                {{ order.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Email </span>
              <span class="dictionary__value">
                {{ order.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Способ оплаты </span>
              <span class="dictionary__value">
                {{ order.paymentType }}
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li
              class="cart__order"
              v-for="product in order.basket.items"
              :key="product.id"
            >
              <h3>{{ product.product.title }}</h3>
              <b>{{ product.price * product.quantity }} ₽</b>
              <span>Артикул: {{ product.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>
              Доставка: <b>{{ order.deliveryType.price }} ₽</b>
            </p>
            <p>
              Итого: <b>{{ totalAmoun }}</b> товара на сумму
              <b>
                {{ order.totalPrice + Number(order.deliveryType.price) }} ₽</b
              >
            </p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
export default {
  data() {
    return {
      productData: null,
    };
  },
  created() {
    if (
      this.$store.state.orderInfo &&
      this.$store.state.orderInfo.id === this.$route.params.id
    ) {
      return;
    }
    this.$store.dispatch("loadOrderInfo", this.$route.params.id);
  },
  computed: {
    order() {
      return this.$store.state.orderInfo || null; // ? this.$store.state.orderInfo : {}
    },
    basketProducts() {
      return this.order.basket.items ? this.order.basket.items.slice() : [];

      // return Array.from(this.order).reduce((acc, basket) => basket.items.quantity + acc, 0);
    },
    totalAmoun() {
      return this.basketProducts.reduce(
        (acc, items) => items.quantity + acc,
        0
      );
    },
  },
  // watch: {
  //   "$route.params.id": {
  //     handler(){
  //       if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) { return; }
  //       this.$store.dispatch('loadOrderInfo', this.$route.params.id);
  //     },
  //     immediate: true,
  //    },
  //  }
};
</script>
