<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html"> Каталог </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Корзина </a>
        </li>
      </ul>
      <div class="content__row">
        <h1 class="content__title">Корзина</h1>
        <span class="content__info"> {{ totalAmoun }} товара </span>
      </div>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div v-if="$store.state.productAddCart === true" class="content__title">
          Загрузка товаров... <img src="img/svg/1488.gif" />
        </div>
        <div v-else class="cart__field">
          <ul class="cart__list">
            <CartItem
              v-for="item in products"
              :key="item.productId"
              :item="item"
            />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPricePretty }} ₽</span>
          </p>

          <router-link
            v-slot="{ navigate }"
            :to="{ name: 'order' }"
            v-show="products.length > 0"
            custom
          >
            <button
              @click="navigate"
              :disabled="emptyBasket"
              class="cart__button button button--primery"
              type="button"
            >
              Оформить заказ
            </button></router-link
          >
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapGetters } from "vuex";
import CartItem from "@/components/CartItem";
import numberFormat from "@/helpers/numbersFormat";

export default {
  components: { CartItem },
  computed: {
    ...mapGetters({
      products: "cartDetailProducts",
      totalPrice: "catrTotalPrise",
      totalAmoun: "catrTotalAmount",
    }),
    totalPricePretty() {
      return numberFormat(this.totalPrice);
    },
    emptyBasket(){
      return this.totalAmoun === 0;
    }
  },
};
</script>
