<template>
  <main class="content container" v-if="productsLoading">
    Загрузка товаров...
  </main>
  <main class="content container" v-else-if="!productData">
    Не удалось загрузить товар
  </main>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link
            class="breadcrumbs__link"
            :to="{ name: 'main', query: { categoryId: category.id } }"
          >
            {{ category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="img" :alt="product.title" />
        </div>
        <ul class="pics__list">
          <li class="pics__item" v-for="image in productImage" :key="image">
            <img
              width="98"
              height="98"
              :src="image"
              @click="switchImage"
              :alt="product.title"
            />
          </li>
        </ul>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ product.id }}</span>
        <h2 class="item__title">
          {{ product.title }}
        </h2>
        <div class="item__form">
          <form
            class="form"
            action="#"
            method="POST"
            @submit.prevent="addToCart"
          >
            <div class="item__row item__row--center">
              <div class="form__counter">
                <button
                  type="button"
                  aria-label="Убрать один товар"
                  @click="deleteProductAmount()"
                  :disabled="productAmount <= 0"
                >
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" v-model.number="productAmount" />

                <button
                  type="button"
                  aria-label="Добавить один товар"
                  @click="addProductAmount()"
                >
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>
              <b class="item__price"> {{ pricePretty }} ₽ </b>
            </div>

            <div class="item__row">
              <fieldset class="form__block">
                <legend class="form__legend">Цвет:</legend>
                <ul class="colors colors--black">
                  <li
                    class="colors__item"
                    v-for="(color, index) in productColors"
                    :key="color.id"
                  >
                    <label class="colors__label">
                      <input
                        class="colors__radio sr-only"
                        type="radio"
                        :value="color.id"
                        v-model="productColorId"
                        :checked="index === 0"
                        @change="cahngeImageAndColor(index)"
                      />
                      <span
                        class="colors__value"
                        :style="{ backgroundColor: color.code }"
                      >
                      </span>
                    </label>
                  </li>
                </ul>
              </fieldset>

              <fieldset class="form__block">
                <legend class="form__legend">Размер</legend>
                <label
                  class="form__label form__label--small form__label--select"
                >
                  <select
                    class="form__select"
                    type="text"
                    name="category"
                    v-model.number="productSizeId"
                  >
                    <option
                      :value="sizes.id"
                      v-for="sizes in product.sizes"
                      :key="sizes.id"
                    >
                      {{ sizes.title }}
                    </option>
                  </select>
                </label>
              </fieldset>
            </div>

            <button
              class="item__button button button--primery"
              type="submit"
              :disabled="productAddSending"
              style="margin-bottom: 20px"
            >
              В корзину
            </button>

            <div v-show="productAdded" class="header__info">
              Товар добавлен в корзину
            </div>
            <div v-show="productAddSending" class="header__info">
              Добавлем Товар в корзину <img src="img/svg/1496.gif" />
            </div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a
              class="tabs__link"
              @click="changeProductInfo"
              :class="{ 'tabs__link--current': productInfo }"
            >
              Информация о товаре
            </a>
          </li>
          <li class="tabs__item">
            <a
              class="tabs__link"
              @click="changeDeliveryInfo"
              :class="{ 'tabs__link--current': deliveryInfo }"
            >
              Доставка и возврат
            </a>
          </li>
        </ul>

        <div class="item__content" v-show="productInfo">
          <h3>Состав:</h3>

          <p>
            60% хлопок<br />
            30% полиэстер<br />
          </p>

          <h3>Уход:</h3>

          <p>
            Машинная стирка при макс. 30ºC короткий отжим<br />
            Гладить при макс. 110ºC<br />
            Не использовать машинную сушку<br />
            Отбеливать запрещено<br />
            Не подвергать химчистке<br />
          </p>
        </div>

        <div class="item__content" v-show="deliveryInfo">
          <h3>Доставка:</h3>

          <p>
            1. Курьерская доставка по Москве и Московской области – 290 ₽<br />
            2.Самовывоз из магазина. Список и адреса магазинов Вы можете
            посмотреть здесь<br />
          </p>

          <h3>Возврат:</h3>

          <p>
            Любой возврат должен быть осуществлен в течение Возвраты в магазине
            БЕСПЛАТНО.<br />
            Вы можете вернуть товары в любой магазин. Магазин должен быть
            расположен в стране, в которой Вы осуществили покупку. БЕСПЛАТНЫЙ
            возврат в Пункт выдачи заказов.<br />
            Для того чтобы вернуть товар в одном из наших Пунктов выдачи
            заказов, позвоните по телефону 8 800 600 90 09<br />
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import axios from "axios";
import { mapActions } from "vuex";
import { API_BASE_URL } from "../config";
import numberFormat from "@/helpers/numbersFormat";

export default {
  data() {
    return {
      productAmount: 1,
      productColorId: null,
      productSizeId: null,
      productData: null,

      img: null,

      productsLoading: false,
      productsLoadingFailed: false,

      productAdded: false,
      productAddSending: false,

      productInfo: true,
      deliveryInfo: false,
    };
  },
  computed: {
    product() {
      return this.productData;
    },
    category() {
      return this.productData.category;
    },
    productColors() {
      const productColors = this.product.colors.map((item) => item.color);
      return productColors;
    },
    productImage() {
      const productImage = this.product.colors.map((item) => {
        if (item.gallery === null) {
          return [];
        }
        return item.gallery;
      });
      if (productImage === null) {
        return "img/nophoto.jpeg";
      }
      return productImage.map((item) => {
        if (item.length === 0) {
          return "img/nophoto.jpeg";
        }
        return item.map((item) => item.file.url);
      });
    },
    pricePretty() {
      return numberFormat(this.product.price);
    },
  },
  methods: {
    ...mapActions(["addProductToCart"]),

    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;

      this.addProductToCart({
        productId: this.product.id,
        amount: this.productAmount,
        colorId: this.productColorId,
        sizeId: this.productSizeId,
      }).then(() => {
        this.productAdded = true;
        this.productAddSending = false;
      });
    },
    addProductAmount() {
      this.productAmount++;
    },
    deleteProductAmount() {
      this.productAmount--;
    },
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      axios
        .get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
        .then((response) => {
          this.productData = response.data;
          this.productColorId = response.data.colors[0].color.id;
          this.productSizeId = response.data.sizes[0].id;
          this.img = this.productImage[0];
        })
        .catch(() => (this.productsLoadingFailed = true))
        .then(() => (this.productsLoading = false));
    },
    switchImage(e) {
      const element = e.target;
      this.img = element.src;

      this.productImage.find((p, index) => {
        if (String(p) === element.src)
          this.productColorId = this.productColors[index].id;
      });
    },
    cahngeImageAndColor(index) {
      this.img = this.productImage[index];
    },
    changeDeliveryInfo() {
      (this.productInfo = false), (this.deliveryInfo = true);
    },
    changeProductInfo() {
      (this.productInfo = true), (this.deliveryInfo = false);
    },
  },
  created() {
    this.loadProducts();
  },
  beforeRouteUpdate() {
    this.loadProducts();
  },
};
</script>
