<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img
        :src="productImage"
        width="120"
        height="120"
        :alt="item.product.title"
      />
    </div>
    <h3 class="product__title">{{ item.product.title }}</h3>
    <p class="product__info product__info--color">
      Цвет:
      <span>
        <i :style="{ background: item.color.color.code }"></i>
        {{ item.color.color.title }}
      </span>
      <span class="product__info"> Размер: {{ item.size.title }} </span>
    </p>
    <span class="product__code"> Артикул: {{ item.product.id }} </span>

    <div class="product__counter form__counter">
      <button
        type="button"
        aria-label="Убрать один товар"
        @click="amount--"
        :disabled="amount <= 0"
      >
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="number" v-model.number="amount" name="count" />

      <button type="button" aria-label="Добавить один товар" @click="amount++">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price"> {{ pricePretty }} ₽ </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteCartProduct(item.itemId)"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import { mapActions } from "vuex";
import numberFormat from "@/helpers/numbersFormat";

export default {
  data() {
    return {
      basketLoading: false,
    };
  },
  props: ["item"],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch("updateCartProductAmount", {
          productId: this.item.itemId,
          amount: value,
        });
      },
    },
    productImage() {
      return this.item.color.gallery.map((item) => item.file.url);
    },
    pricePretty() {
      return numberFormat(this.item.product.price * this.item.amount);
    },
  },
  methods: {
    ...mapActions(["deleteCartProduct"]),
  },
};
</script>
