import Vuex, { createStore } from "vuex";
import axios from "axios";
import { API_BASE_URL } from "../config";

export default createStore({
  state: {
    cartProducts: [],

    userAccessKey: null,
    cartProductsData: [],

    productAddCart: false,

    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductAmount(state, { productId, amount }) {
      const item = state.cartProducts.find(
        (item) => item.productId === productId
      );

      if (item) {
        item.amount = amount;
      }
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter(
        (item) => item.productId !== productId
      );
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        itemId: item.id,
        amount: item.quantity,
      }));
    },
  },
  getters: {
    cartDetailProducts(state) {
      return state.cartProducts.map((item) => {
        const { product, id, color, size } = state.cartProductsData.find(
          (p) => p.id === item.itemId
        );

        return {
          ...item,
          itemId: id,
          size: { ...size },
          color: { ...color },
          product: {
            ...product,
            image: product.colors.map((item) => {
              return item.gallery.map((item) => {
                return item.file.url;
              });
            }),
          },
        };
      });
    },
    catrTotalPrise(state, getters) {
      return getters.cartDetailProducts.reduce(
        (acc, item) => item.product.price * item.amount + acc,
        0
      );
    },
    catrTotalAmount(state, getters) {
      return getters.cartDetailProducts.reduce(
        (acc, item) => item.amount + acc,
        0
      );
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      return axios
        .get(`${API_BASE_URL}/api/orders/${orderId}`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit("updateOrderInfo", response.data);
        });
    },
    loadCart(context) {
      context.state.productAddCart = true;
      return new Promise((resolve) => setTimeout(resolve, 3000))
        .then(() =>
          axios
            .get(`${API_BASE_URL}/api/baskets`, {
              params: {
                userAccessKey: context.state.userAccessKey,
              },
            })
            .then((response) => {
              if (!context.state.userAccessKey) {
                localStorage.setItem(
                  "userAccessKey",
                  response.data.user.accessKey
                );
                context.commit(
                  "updateUserAccessKey",
                  response.data.user.accessKey
                );
              }
              context.commit("updateCartProductsData", response.data.items);
              context.commit("syncCartProducts");
            })
        )
        .then(() => (context.state.productAddCart = false));
    },
    addProductToCart(context, { productId, amount, colorId, sizeId }) {
      context.state.productAddCart = true;
      return new Promise((resolve) => setTimeout(resolve, 3000))
        .then(() =>
          axios
            .post(
              `${API_BASE_URL}/api/baskets/products`,
              {
                productId,
                quantity: amount,
                colorId,
                sizeId,
              },
              {
                params: {
                  userAccessKey: context.state.userAccessKey,
                },
              }
            )
            .then((response) => {
              context.commit("updateCartProductsData", response.data.items);
              context.commit("syncCartProducts");
            })
        )
        .then(() => (context.state.productAddCart = false));
    },
    updateCartProductAmount(context, { productId, amount }) {
      context.commit("updateCartProductAmount", { productId, amount });

      if (amount < 1) {
        return;
      }

      return axios
        .put(
          `${API_BASE_URL}/api/baskets/products`,
          {
            basketItemId: productId,
            quantity: amount,
          },
          {
            params: {
              userAccessKey: context.state.userAccessKey,
            },
          }
        )
        .then((response) => {
          context.commit("updateCartProductsData", response.data.items);
          context.commit("syncCartProducts");
        });
    },
    deleteCartProduct(context, productId) {
      return axios
        .delete(`${API_BASE_URL}/api/baskets/products`, {
          data: {
            basketItemId: productId,
          },

          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit("updateCartProductsData", response.data.items);
          context.commit("syncCartProducts");
        });
    },
  },
});
