<template>
  <main class="content container">
    <div class="content__top">
      <div class="content__row">
        <h1 class="content__title">Каталог</h1>
        <span class="content__info"> {{ quantityProducts }} товаров </span>
      </div>
    </div>

    <div class="content__catalog">
      <ProductFilter
        v-model:price-from="filterPriceFrom"
        v-model:price-to="filterPriceTo"
        v-model:category-id="filterCategoryId"
        v-model:material-ids="filterMaterialIds"
        v-model:season-ids="filterSeasonIds"
      />
      <section class="catalog">
        <div v-show="productsLoading" class="content__title">
          Загрузка товаров... <img src="img/svg/1488.gif" />
        </div>
        <div v-show="productsLoadingFailed" class="content__title">
          Произошла ошибка при загрузке товаров.
          <button
            @click.prevent="loadProducts"
            class="button--primery"
            style="padding: 10px 15px"
          >
            Попробовать еще раз
          </button>
        </div>
        <ProductList
          :products="products"
          v-show="this.productsLoading === false"
        />

        <BasePagination
          v-model="page"
          :count="countProducts"
          :per-page="productsPerPage"
        />
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from "@/components/ProductList";
import BasePagination from "@/components/BasePagination";
import ProductFilter from "@/components/ProductFilter";
import axios from "axios";
import { API_BASE_URL } from "../config";

export default {
  components: { ProductList, BasePagination, ProductFilter },
  props: ["categoryId"],
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterMaterialIds: [],
      filterSeasonIds: [],

      page: 1,
      productsPerPage: 12,

      productsData: null,
      quantityProducts: 0,

      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData
        ? this.productsData.items.map((product) => ({
            ...product,
            image: product.colors.map((item) => {
              if (item.gallery === null) {
                return ["img/nophoto.jpeg"];
              }
              return item.gallery.map((item) => {
                return item.file.url ? item.file.url : [];
              });
            }),
            colors: product.colors.map((item) => {
              return item.color;
            }),
          }))
        : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(`${API_BASE_URL}/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              materialIds: this.filterMaterialIds,
              seasonIds: this.filterSeasonIds,
            },
          })
          .then((response) => (this.productsData = response.data))
          .catch(() => (this.productsLoadingFailed = true))
          .then(() => (this.productsLoading = false));
      }, 2000);
    },
    loadQuantityProducts() {
      axios
        .get(`${API_BASE_URL}/api/products`)
        .then(
          (response) => (this.quantityProducts = response.data.items.length)
        );
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterMaterialIds() {
      this.loadProducts();
    },
    filterSeasonIds() {
      this.loadProducts();
    },
    "$route.query.categoryId": {
      handler() {
        this.filterCategoryId = this.$route.query.categoryId
          ? this.$route.query.categoryId
          : 0;
      },
      immediate: true,
    },
  },
  created() {
    this.loadProducts();
    this.loadQuantityProducts();
  },
};
</script>
