<template>
  <li
    v-bind="$attrs"
    v-for="(product, index) in productsNormalized"
    :key="product.id"
  >
    <router-link
      class="catalog__pic"
      :to="{ name: 'product', params: { id: product.id } }"
    >
      <img :src="productImage[index]" :alt="product.title" />
    </router-link>
    <router-link :to="{ name: 'product', params: { id: product.id } }">
      <h3 class="catalog__title">
        <a href="#">
          {{ product.title }}
        </a>
      </h3>

      <span class="catalog__price"> {{ product.pricePretty }} ₽ </span>
    </router-link>
    <ul class="colors colors--black">
      <li
        class="colors__item"
        v-for="(color, index) in product.colors"
        :key="color.id"
      >
        <label class="colors__label">
          <input
            class="colors__radio sr-only"
            type="radio"
            :value="color.id"
            v-model="checkedColor"
            @click="changeProductImage(product, index)"
          />
          <span class="colors__value" :style="{ backgroundColor: color.code }">
          </span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script>
import numberFormat from "@/helpers/numbersFormat";

export default {
  data() {
    return {
      productImage: [],
      checkedColor: null,
    };
  },
  inheritAttrs: false,
  props: ["products"],
  computed: {
    productsNormalized() {
      return this.products.map((product) => {
        this.productImage = this.products.map((product) => {
          return product.image[0].map((item) => {
            return item;
          });
        });
        return {
          ...product,
          pricePretty: numberFormat(product.price),
        };
      });
    },
  },
  methods: {
    changeProductImage(product, index) {
      this.productsNormalized.find((p, i) => {
        if (p.id === product.id) {
          this.productImage[i] = product.image[index];
        }
      });
    },
  },
};
</script>
