<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'order' }">
            Оформление заказа
          </router-link>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
    </div>

    <section class="cart">
      <form
        class="cart__form form"
        action="#"
        method="POST"
        @submit.prevent="order"
      >
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText
              title="ФИО"
              :error="formError.name"
              placeholder="Введите ваше полное имя"
              v-model="formData.name"
              type="text"
            />
            <BaseFormText
              title="Адрес доставки"
              :error="formError.address"
              placeholder="Введите ваш адрес"
              v-model="formData.address"
              type="text"
            />
            <BaseFormText
              title="Телефон"
              :error="formError.phone"
              placeholder="Введите ваш телефон"
              v-model="formData.phone"
              type="tel"
            />
            <BaseFormText
              title="Email"
              :error="formError.email"
              placeholder="Введи ваш Email"
              v-model="formData.email"
              type="email"
            />
            <BaseFormTextarea
              title="Комментарий к заказу"
              :error="formDataComment"
              placeholder="Ваши пожелания"
              v-model="formData.comment"
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li
                class="options__item"
                v-for="(delivery, index) in deliveries"
                :key="delivery.id"
              >
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    :value="delivery.id"
                    v-model="deliveryId"
                    @change="currentDeliveryPrice(delivery.price)"
                    :checked="index === 0"
                  />
                  <span class="options__value">
                    {{ delivery.title }} <b>{{ delivery.price }} ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li
                class="options__item"
                v-for="(payment, index) in payments"
                :key="payment.id"
              >
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    :value="payment.id"
                    v-model="paymentId"
                    :checked="index === 0"
                  />
                  <span class="options__value">
                    {{ payment.title }}
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li
              class="cart__order"
              v-for="product in productNormalized"
              :key="product.id"
            >
              <h3>{{ product.product.title }}</h3>
              <b> {{ product.pricePretty }} ₽</b>
              <span>Артикул: {{ product.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>
              Доставка: <b>{{ deliveryPrice }} ₽</b>
            </p>
            <p>
              Итого: <b>{{ totalAmoun }}</b> товара на сумму
              <b>{{ totalPricePretty }} ₽</b>
            </p>
          </div>

          <button
            class="cart__button button button--primery"
            type="submit"
            style="margin-bottom: 15px"
          >
            Оформить заказ
          </button>
          <div v-show="sendingOrder" class="header__info">
            Отправляем заказ... <img src="img/svg/1496.gif" />
          </div>
        </div>
        <div class="cart__error form__error-block" v-show="sendingOrderError">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from "@/components/BaseFormText";
import BaseFormTextarea from "@/components/BaseFormTextarea";
import axios from "axios";
import { mapGetters } from "vuex";
import { API_BASE_URL } from "../config";
import numberFormat from "@/helpers/numbersFormat";

export default {
  components: { BaseFormText, BaseFormTextarea },
  data() {
    return {
      formData: {},
      formDataComment: "",
      formError: {},
      formErrorMessage: "",

      sendingOrder: false,
      sendingOrderError: false,

      deliveries: [],
      deliveryId: null,
      deliveryPrice: null,
      payments: [],
      paymentId: null,
    };
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = "";
      this.sendingOrder = true;
      this.sendingOrderError = false;

      axios
        .post(
          `${API_BASE_URL}/api/orders`,
          {
            ...this.formData,
            deliveryTypeId: this.deliveryId,
            paymentTypeId: this.paymentId,
            comment: this.formDataComment,
          },
          {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          }
        )
        .then((response) => {
          this.$store.commit("resetCart");
          this.$store.commit("updateOrderInfo", response.data);
          this.$router.push({
            name: "orderInfo",
            params: { id: response.data.id },
          });
          this.sendingOrder = false;
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          if (error.response.data.error.message === "") {
            this.formErrorMessage =
              "Похоже произошла ошибка. Попробуйте отправить снова или перезагрузите страницу.";
          } else {
            this.formErrorMessage = error.response.data.error.message;
          }
          this.sendingOrder = false;
          this.sendingOrderError = true;
        });
    },
    loadDeliveries() {
      axios.get(`${API_BASE_URL}/api/deliveries`).then((response) => {
        this.deliveries = response.data;
        this.deliveryId = this.deliveries[0].id;
        this.deliveryPrice = this.deliveries[0].price;
      });
    },
    loadPayments() {
      axios
        .get(`${API_BASE_URL}/api/payments`, {
          params: {
            deliveryTypeId: this.deliveryId,
          },
        })
        .then((response) => {
          this.payments = response.data;
          this.paymentId = this.payments[0].id;
        });
    },
    currentDeliveryPrice(price) {
      this.deliveryPrice = price;
    },
  },
  computed: {
    ...mapGetters({
      products: "cartDetailProducts",
      totalPrice: "catrTotalPrise",
      totalAmoun: "catrTotalAmount",
    }),
    totalPricePretty() {
      return numberFormat(this.totalPrice + Number(this.deliveryPrice));
    },
    productNormalized() {
      return this.products.map((product) => {
        return {
          ...product,
          pricePretty: numberFormat(product.product.price),
        };
      });
    },
  },
  created() {
    this.loadDeliveries();
  },
  watch: {
    deliveryId() {
      this.loadPayments();
    },
  },
};
</script>
