<template>
<ul class="catalog__pagination pagination">
          <li class="pagination__item">
            <button class="pagination__link pagination__link--arrow" aria-label="Предыдущая страница" @click="paginateBack()" :disabled="page === 1" :class="{'pagination__link--disabled': page == 1}" style="cursor:pointer">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-left"></use>
              </svg>
            </button>
          </li>
          <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
            <a herf="#" class="pagination__link" :class="{'pagination__link--current': pageNumber === page}" @click="paginate(pageNumber)" style="cursor:pointer">
              {{ pageNumber }}
            </a>
          </li>
          <li class="pagination__item">
            <button class="pagination__link pagination__link--arrow" href="#" aria-label="Следующая страница" @click="paginateForward()" :disabled="page === pages" :class="{'pagination__link--disabled': page === pages}" style="cursor:pointer">
              <svg width="8" height="14" fill="currentColor">
                <use xlink:href="#icon-arrow-right"></use>
              </svg>
            </button>
          </li>
        </ul>
</template>

<script>
export default {
  props: ['modelValue', 'count', 'perPage'],
  computed: {
    page() {
      return this.modelValue;
    },
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
  },
  methods: {
    paginate(page) {
      this.$emit('update:modelValue', page);
    },
    paginateForward() {
      this.$emit('update:modelValue', this.page + 1);
    },
    paginateBack() {
      this.$emit('update:modelValue', this.page - 1);
    },
  },
};
</script>
